<template lang="html">
  <div class="Userinfo">
    <Row>
      <Col  class="Userinfo-title">{{$t("common.Basic_user_information")}}</Col>
    </Row>
    <table>
      <colgroup></colgroup>
      <tbody>
        <tr>
          <th>{{$t("common.user_number")}}</th>
          <td>{{msg.user_number}}</td>
          <th>{{$t("common.Nickname")}}</th>
          <td>{{msg.username}}</td>
          <th>{{$t("common.E_mail")}}</th>
          <td></td>
        </tr>
        <tr>
          <th>{{$t("common.realname")}}</th>
          <td>{{msg.realname}}</td>
          <th>{{$t("common.ID_number")}}</th>
          <td></td>
          <th rowspan="3">{{$t("common.Security_avatar")}}</th>
          <td rowspan="3"></td>
        </tr>
        <tr>
          <th>{{$t("common.Contact_number")}}</th>
          <td>{{msg.phonenum}}</td>
          <th>{{$t("common.if_real_name")}}</th>
          <td>{{msg.if_real_name}}</td>
        </tr>
        <tr>
          <th>{{$t("common.if_bind_bank")}}</th>
          <td>{{msg.if_bind}}</td>
          <th>{{$t("common.Bank_card_number")}}</th>
          <td></td>

        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data (){
    return {
      msg:{
        SID:  '',
        user_number: '',
        username: '',
        phonenum: '',
        if_real_name:'',
        realname:'',
        if_bind:'',
        login_time:''
      }
    }
  },
  methods:{
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.msg = routerParams
        }

        console.log(this.msg)
    }
  },
  mounted(){
    this.getParams()
  }
}
</script>

<style lang="css">
.Userinfo{
  padding-top: 70px;
  width: 1200px;
  margin: 0 auto;
}
.Userinfo table{
  width: 100%;
  border-collapse:collapse;
}

.Userinfo table tr:nth-child(odd){
  background-color:#fff;
}
.Userinfo table th{
  width: 120px;
  height: 30px;
  text-align: right;
  padding-right: 5px;
  border: 1px solid #DDDEE1;
  background-color:#fff;
}
.Userinfo table td{
  width: 200px;
  height: 30px;
  text-align: left;
  padding-left: 5px;
  border: 1px solid #DDDEE1;
  background-color:#fff;
}
.Userinfo .Userinfo-title{
  text-align: left;
  padding-left: 30px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  background: #fff;
  margin-bottom: 20px;
}
</style>



